body {
  background-color: #282c34;
}

.App {
  margin-left: 300px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
}
a:hover {
  color: #0056b3;
  text-decoration: underline;
}
a:active, a:focus, a:hover {
  text-decoration: none;
  outline: none;
}

.mi-header-toggler {
  position: absolute;
  left: 100%;
  top: 20px;
  padding: 0;
  height: 50px;
  width: 50px;
  text-align: center;
  font-size: 1.6rem;
  background: #191d2b;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.menu-inner{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  overflow-y: auto;
}

.menu-list {
  list-style: none;
  text-align: center;
  display: block;
  width: 100%;
  padding: 0;
}

.menu-list li a {
  text-transform: uppercase;
  font-size: .94rem;
  letter-spacing: 1px;
  display: block;
  padding: 5px 0;
  color: #a4acc4;
  font-weight: 600;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.menu-list li a.active:before {
  background: #037fff;
  width: 100%;
  visibility: visible;
}

.menu-list li a:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 100%;
  height: 100%;
  width: 0;
  background: rgba(3,127,255,.2);
  visibility: hidden;
  z-index: -1;
}
.menu-list li a:after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  height: 100%;
  width: 0;
  background: rgba(3,127,255,.2);
  visibility: hidden;
  z-index: -1;
}
.menu-list li a:hover:before {
  width: 100%;
  visibility: visible;
}
.menu-list li a:hover {
  color: #037fff;
}
.menu-list li a, .menu-list li a:before {
  -webkit-transition: all .4s ease-out;
  transition: all .4s ease-out;
}

.menu-list li a.active {
  color: #fff;
}


